/* General Styles */
html, body {
    height: 100%;
    margin: 0;
    padding: 0;
}

body {
    font-family: Arial, sans-serif;
    background-color:#f1ecf3;
}



/* Header */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

header {
    color: white;
    text-align: center;
    font-size: calc(10px + 2vmin);
    background-image: url('https://images.unsplash.com/photo-1608982398895-28ae86b19ae4?w=500&auto=format&fit=crop&q=60&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8NjN8fHBlbmFuZyUyQyUyMG1hbGF5c2lhfGVufDB8fDB8fHww');
    background-size: cover;
    background-position: center;
    background-attachment: fixed; /* Keeps the background fixed while scrolling */
    background-repeat: no-repeat; /* Prevents tiling of the image */
    min-height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative; /* Added to position the overlay */
}

/* Gradient Overlay */
header::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5); /* Adds dark overlay */
    z-index: 1; /* Ensures the overlay is beneath the text */
}

/* Header text */
header h1.custom-font {
    font-size: 52px;
    font-family: 'Quicksand', serif;
    font-weight: bold;
    color: #FFFFFF; /* White color for better contrast on the overlay */
    margin-bottom: 10px;
    z-index: 2; /* Ensures the header text stays on top of the overlay */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7); /* Adds a shadow for better readability */
}

/* Subheading text */
header p {
    font-size: 20px;
    line-height: 0.8;
    color: #FFFFFF; /* White for better contrast */
    margin: 10px;
    font-weight: bold;
    z-index: 2; /* Ensures the paragraph stays on top */
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.7); /* Adds a subtle shadow effect */
}

/* Optional: Add a hover effect to the heading */
header h1.custom-font:hover {
    color: #FFD700; /* Gold color on hover */
    transform: scale(1.05); /* Slightly scales up on hover */
    transition: transform 0.3s ease, color 0.3s ease; /* Smooth transition */
}



/* Navigation */
nav {
    display: flex;
    justify-content: center;
    background-color: white;
    flex-wrap: wrap;
}

nav a {
    color: #000000;
    text-decoration: none;
    padding: 15px 20px;
    margin: 0 5px;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

nav a:hover {
    background-color: rgba(0, 0, 0, 0.1);
    transform: scale(1.1);
}


/* Dynamic Content Styles */
@keyframes slideIn {
    from {
        transform: translateX(-100%);
    }
    to {
        transform: translateX(0);
    }
}

#dynamic-content {
    margin: 20px auto;
    max-width: 1500px;
    border-radius: 8px;
    padding: 20px;
    animation: slideIn 0.8s ease-out;
}


.about-us-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
    color: #333;
    text-align: center;
}

.about-us-container h1 {
    font-size: 2.5em;
    color: #222;
    margin-bottom: 20px;
}

.about-us-container p {
    font-size: 1.1em;
    line-height: 1.6;
    margin-bottom: 30px;
    color: #666;
}

.about-image {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-top: 40px;
    text-align: left;
}

.about-image-img {
    width: 100%;
    max-width: 400px;
    height: auto;
    border-radius: 8px;
}

.about-description {
    max-width: 500px;
    font-size: 1.1em;
    color: #444;
}

.about-description h3 {
    font-size: 1.5em;
    color: #333;
    margin-bottom: 15px;
}

.about-description p {
    font-size: 1.1em;
    color: #666;
    line-height: 1.6;
}

/* New Styles for Image and Text Pairing */
.image-text-pair {
    display: flex;
    align-items: center;
    margin-bottom: 80px;
    border-radius: 20px;
    padding: 10px;
    justify-content: center;
    background-image: url('https://images.pexels.com/photos/10526919/pexels-photo-10526919.jpeg?auto=compress&cs=tinysrgb&w=600');
}

.image-text-pair img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    object-fit: cover;
    background-position: center;
}

.image-text-pair .text {
    flex: 1;
    margin-left: 20px;
    text-align: left;
}

.image-text-pair:hover {
    transform: scale(1.05);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.image-text-pair + .image-text-pair {
    border-top: 2px solid #000000;
    padding-top: 20px;
    margin-top: 20px;
    width: 100%;
}

/* Footer */
@keyframes footerFadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

footer {
    background-color: white;
    color: #000000;
    text-align: center;
    padding: 10px;
    width: 100%;
    animation: footerFadeIn 1s ease-in-out;
}

/* Media Queries */
@media screen and (max-width: 768px) {
    nav {
        flex-direction: column;
        align-items: center;
    }
}

@media screen and (max-width: 480px) {
    header h1 {
        font-size: 1.5rem;
    }

    nav a {
        font-size: 1rem;
    }
}




